<template>
  <div class="login">
    <!-- 头部 -->
    <van-nav-bar title="会员登陆" left-arrow @click-left="$router.go(-1)"></van-nav-bar>
    <!-- 主体 -->
    <div class="container">
      <div class="title">
        <h3>手机号登录</h3>
        <p>未注册的手机号登录后将自动注册</p>
      </div>

      <div class="form">
        <div class="form-item">
          <input v-model="mobile" type="text" class="inp" placeholder="请输入手机号码" maxlength="11">
        </div>
        <div class="form-item">
          <input v-model="picCode" class="inp" maxlength="5" placeholder="请输入图形验证码" type="text">
          <img v-if="picUrl" :src="picUrl" alt="" @click="getPicCode">
        </div>
        <div class="form-item">
          <input v-model="smsCode" class="inp" placeholder="请输入短信验证码" type="text">
          <button @click="getCode">
            {{ second===totalSencond ? '获取验证码' : second+'秒后重新发送'}}
          </button>
        </div>
      </div>
      <div @click="login" class="login-btn">登录</div>
    </div>
  </div>
</template>

<script>
import { getPicCode, getMsgCode, codeLogin } from '@/api/login'
export default {
  name: 'LoginIndex',
  data () {
    return {
      picKey: '', // 图形验证码的key
      picUrl: '', // 图形验证码的地址
      totalSencond: 60, // 倒计时总时间
      second: 60, // 当前秒数，开定时器对其进行减1操作
      timer: null, // 定时器id
      mobile: '', // 手机号
      picCode: '', // 用户输入的图形验证码
      smsCode: '' // 用户输入的短信验证码
    }
  },
  created () {
    // 获取图形验证码
    this.getPicCode()
  },
  methods: {
    async getPicCode () {
      const { data: { base64, key } } = await getPicCode()
      this.picUrl = base64
      this.picKey = key

      // Toast({
      //   message: '获取图形验证码成功',
      //   duration: 1000
      // })
    },

    // 校验
    validFn () {
      if (!/^1[3-9]\d{9}$/.test(this.mobile)) {
        this.$toast('请输入正确的手机号')
        return false
      }
      if (!/^\w{4}$/.test(this.picCode)) {
        this.$toast('请输入正确的图形验证码')
        return false
      }
      return true
    },

    // 获取短信验证码
    async getCode () {
      if (!this.validFn()) {
        // 校验不通过，直接返回
        return
      }

      // 当前没有定时器开着，且totalSencond===second,才能开启倒计时
      if (!this.timer && this.totalSencond === this.second) {
        await getMsgCode(this.picCode, this.picKey, this.mobile)

        this.$toast('短信发送成功，注意查收')
        // 开启倒计时
        this.timer = setInterval(() => {
          this.second--

          if (this.second <= 0) {
            clearInterval(this.timer)
            this.timer = null
            this.second = this.totalSencond
          }
        }, 1000)
      }
    },

    async login () {
      if (!this.validFn()) {
        return
      }

      if (!/^\d{6}$/.test(this.smsCode)) {
        this.$toast('请输入正确的短信验证码')
        return
      }

      const res = await codeLogin(this.mobile, this.smsCode)
      this.$store.commit('user/setUserInfo', res.data)
      console.log(res)

      this.$toast('登录成功')

      // 进行判断，看地址栏有无跳回地址
      const url = this.$route.query.backUrl || '/'
      this.$router.replace(url)
    }
  },

  destroyed () {
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
.container{
  padding:49px 29px;

  .title{
    margin-bottom: 20px;
    h3{
      font-size: 26px;
      font-weight: normal;
    }
    p{
      line-height: 40px;
      font-size: 14px;
      color: #b8b8b8;
    }
  }

  .form-item{
    border-bottom: 1px solid #f3f1f2;
    padding: 8px;
    margin-bottom: 14px;
    display: flex;
    align-items: center;
    .inp{
      display: block;
      border:none;
      outline: none;
      height: 32px;
      font-size: 14px;
      flex: 1;
    }
    img{
      width: 94px;
      height: 31px;
    }
    button{
      height: 31px;
      border:none;
      font-size: 13px;
      color: #cea26a;
      background-color: transparent;
      padding-right: 9px;
    }
  }

  .login-btn{
    width: 100%;
    height: 42px;
    margin-top: 39px;
    background: linear-gradient(90deg,#ecb53c,#ff9211);
    color: #fff;
    border-radius: 39px;
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, .1);
    letter-spacing: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
</style>
